@font-face {
    font-family: 'Komika';
    src: url('../../fonts/KOMTITT_.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

.scrollButton {
    background: var(--accent-blue)
}

.logoDiv {
    position: absolute;
    animation: fall 2s ease-in-out forwards;
}

@keyframes fall {
    0% {
        top: -200px; /* Start above the viewport */
    }
    50% {
        top: 30vh; /* Move to the middle of the viewport */
    }
    70% {
        top: 25vh; /* Slightly bounce up */
    }
    100% {
        top: 30vh; /* Settle in the middle */
    }
}

.motto {
    font-family: 'Komika', serif;
    position: absolute;
    top: 60vh;
    transform: rotate(-15deg);
}